import jsonp from 'jsonp'

export function base64ToBlob(base64Data) {
	const arr = base64Data.split(',')
	const fileType = arr[0].match(/:(.*?);/)[1]
	const bstr = atob(arr[1])
	let l = bstr.length
	const u8Arr = new Uint8Array(l)

	while (l--) {
		u8Arr[l] = bstr.charCodeAt(l)
	}
	return new Blob([u8Arr], {
		type: fileType
	})
}

export function blobToFile(newBlob, fileName) {
	newBlob.lastModifiedDate = new Date()
	newBlob.name = fileName
	return newBlob
}

export function checkUrl(url) {
	const urlRegExp =
		/^(http|https|ftp)\:\/\/([a-zA-Z0-9\.\-]+(\:[a-zA-Z0-9\.&%\$\-]+)*@)?((25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9])\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[0-9])|([a-zA-Z0-9\-]+\.)*[a-zA-Z0-9\-]+\.[a-zA-Z]{2,4})(\:[0-9]+)?(\/[^/][a-zA-Z0-9\.\,\?\'\\/\+&%\$#\=~_\-@]*)*$/
	return urlRegExp.test(url)
}

export function sugrec(queryString) {
  return new Promise(function (resolve, reject) {
    jsonp('https://www.baidu.com/sugrec?pre=1&p=3&ie=utf-8&json=1&prod=pc&from=pc_web&wd=' + queryString, null, (err, data) => {
      if (err) {
        reject(err);
      } else {
        let result = [];
        if (data.g && data.g.length > 0) {
          result = data.g.map(function (elementOrValue) {
            return {value: elementOrValue.q};
          })
        }
        resolve(result);
      }
    });
  })
}
